  }

.mytamacontainer {
    margin-top: 50px;
    margin: 5px;
}

.mytamawrapper{
     justify-content: center;
    align-items: center;
    display:flex; 
    flex-wrap: wrap;
}

.mytamacardcont{
    margin-top: 30px;
    justify-content: center;
        align-items: center;
        display: flex;
        
 }

.mytamapagetitle {
    color: rgb(64, 176, 224);
    padding: 60px;
    text-align: center;
    font-size: 70px;    
    font-family: 'Schoolbell', cursive;
    /* justify-content: center;
    align-items: center; */
    margin-top: -30px;
  
