.mytamamaincard {
    width: 100%;
    max-width: 350px;
    min-width: 350px;
    height: 230px;
    background-color: rgb(187, 192, 228);
    margin-bottom: 50px;
    border-radius: 10px;
    box-shadow: 0px 2px 10px rgba(75, 70, 179, 0.24);
    border: 2px solid rgba(150, 151, 228, 0.12);
    font-size: 16px;   
    transition: all 0.3s ease;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    cursor: pointer;
    transition: all 0.3s ease;
 }
 
 .icon {
    margin: 0 auto;
    width: 100%;
    height: 200px;
    max-width: 200px;
    border-radius: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    transition: all 0.8s ease;
    background-position: 0px;
    background-size: 400px;
    font-weight: bolder;
    font-size: 14px;
 }
 

 .mytamamaincard .title {
    text-align: center;
    justify-content: center;
    margin-top: 35px;
    margin-bottom: 5px;
    /* margin-left: -10px; */
    color: white;
    font-size: 40px;
    font-weight: 600;
    letter-spacing: 3px;
    font-family: 'Schoolbell', cursive;
    visibility: hidden;
 }
 

 .mytamamaincard .mytamatext {

    font-size: 30;
    margin-block-start: 5px;
    color: white;
    font-weight: 200px;
    opacity: 0;
    max-height:0;
    transition: all 0.3s ease;
    min-width: 250px;
    align-items: center;
 }
 

 .mytamamaincard:hover {
    height: 400px;
    transition: all 0.3s ease;
 }


 .mytamamaincard:hover .mytamacard-img-top{

    margin-top: -140px;
    transition: all 0.8s ease;
 }


 .mytamamaincard:hover .mytamatext {

    transition: all 0.3s ease;
    opacity: 1;
    justify-content: center;
    align-items: center;
    visibility: visible;

 }
 

 .mytamamaincard:hover .icon {

    background-position: -120px;
    transition: all 0.3s ease;
    background-color: rgb(64, 176, 224);
 }
 

 .mytamamaincard:hover .icon i {
    opacity: 1;
    transition: all 0.3s ease;
 }


 .mytamamaincard:hover .title{

    visibility: visible;
    margin-top: 60px;
    transition: all 0.8s ease;
 }

 .mytamatextcont {
     justify-content: center;
     align-items: center;
     text-align: center;
     margin-left: -220px;  
     margin-top: 15px;
     transition: all 0.8s ease;
 }

 .mytamabutton {
    background-color: rgb(187, 192, 228);
     padding: 30px;
     border-radius: 50%;
     margin-top: -50px;
     font-family: 'Schoolbell', cursive;
