 }


 /* ============TRIAL Egg Button === */


 .wrappergachabutton {
    position: absolute;
    top: 275px;
    right: 100px;
    color: cornsilk;
    background-color: rgb(64, 176, 224);
    border-radius:100px;
    width: 125px;
    height: 60px;
    box-shadow: 0px 5px 5px rgba(0,0,0,0.2);
    font-family: 'Schoolbell', cursive;
    animation: bounce 2s infinite;
  }

  .wrappergachabutton:before {
    position: absolute;
    top: 0px;
    left: -500px;
    content: "";
    width: 45px;
    height: 60px;
    background: rgba(255, 255, 255, 0.3);
    transform: skew(-30deg);
    animation: shine 1.5s ease infinite;
  } 
 
 @keyframes shine {
   from {left: -40px;}
   to {left: 140px;}
 }

  @keyframes bounce {
	   0%, 20%, 50%, 80%, 100% {transform: translateY(0);}
	   40% {transform: translateY(-10px);}
	   60% {transform: translateY(-5px);}
   }
  
  .wrappergachabutton .icongachabutton {
    position: relative;
    margin-top: 13px;
    font-size: 23px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    cursor: pointer;
    transition: all 0.2s cubic-bezier(0.68, -0.55, 0.265, 1.55);
  }
  
  .wrappergachabutton .toolgachabutton {
    position: absolute;
    text-align: center;
    top: 0;
    font-size: 14px;
    background-color: #f1fa46;
    color: #ffffff;
    padding: 5px 8px;
    border-radius: 5px;
    box-shadow: 0 10px 10px rgba(0, 0, 0, 0.1);
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s cubic-bezier(0.68, -0.55, 0.265, 1.55);
    width: 100px;
  }
  
  .wrappergachabutton .toolgachabutton::before {
    position: absolute;
    content: "";
    height: 8px;
    width: 8px;
    background-color: #cc7f7f;
    bottom: -3px;
    left: 50%;
    transform: translate(-50%) rotate(45deg);
    transition: all 0.3s cubic-bezier(0.68, -0.55, 0.265, 1.55);
  }
  
  .wrappergachabutton .icongachabutton:hover .toolgachabutton {
    top: -55px;
    opacity: 1;
    visibility: visible;
    pointer-events: auto;
  }
  
  .wrappergachabutton .icoicongachabuttonn:hover span,
  .wrappergachabutton .icongachabutton:hover .toolgachabutton {
    text-shadow: 0px -1px 0px rgba(196, 97, 97, 0.1);
  }
  
  .wrappergachabutton .egggachabutton:hover .toolgachabutton,
  .wrappergachabutton .egggachabutton:hover .toolgachabutton::before {
    background-color: rgb(41, 57, 102);
    color: #fcfafa;
    font-family: 'Schoolbell', cursive;
    font-size: 15px;
  }